// -------------------------------------------------------------------------
//    Main
//--------------------------------------------------------------------------
.main {
  height: 83vh;
  border-top: 2px solid $secondary-color;
  border-bottom: 2px solid $secondary-color;

  section {
    margin: 0 5vw;

    & > div {
      margin: 0.5vh 0;
      
      p {
        font-weight: 500;
      }
      span {
        font-weight: 300;
      }
    }
  }

  // first section
  .current-weather {
    display: flex;
    flex-flow: row nowrap;
    justify-content: space-between;
    border-bottom: 1px solid $secondary-color;

    // left side
    .left-side {
      display: flex;
      flex-flow: column nowrap;
      justify-content: space-around;
      // width: 60%;

      .findlocation {
        position: relative;
        font-size: calc(15px + 2vmin);
        
        input {
          width: 100%;
          border: 1px solid $secondary-color;
          border-radius: 7px;
          color: $theme-color;
          // font-weight: 500;
        }
        &::after {
          content: '\1F50D';
          position: absolute;
          // top: 0.1vh;
          right: 3px;
          color: $secondary-color;
        }
        input:focus div.chooselocation {
          visibility: visible;
        }

        .chooselocation {
          z-index: 1;
          position: absolute;
          margin: 0.3vh 0 0 0;
          width: 90vw;
          border: 1px solid $secondary-color;
          background-color: $theme-color;
          
          p {
            padding: 1vh 0 1vh 2vh;
            font-size: calc(6px + 2vmin);
          }
        }
      }
      
      .close {
        visibility: hidden;
      }
      .date {
        font-size: calc(6px + 2vmin);
        font-weight: 500;
        line-height: 1.5;
      }
    }

    // right side
    .right-side {
      display: flex;
      flex-flow: row nowrap;
      justify-content: space-between;
      align-items: center;

      .current-info {
        display: flex;
        flex-flow: column nowrap;
        justify-content: space-between;
        align-items: flex-end;

        .temperature {
          font-size: calc(22px + 2vmin);
          font-weight: 400;
          line-height: 1;
        }
        .time {
          font-size: calc(6px + 2vmin);
          font-weight: 500;
          line-height: 1.5;
          // letter-spacing: -1px;
        }
      }
    }
  }// .first section

  // second section
  .detailed-weather {

    .weather-type {
      margin: 1vh 0 0 0;
      color: $secondary-color;
      font-size: calc(6px + 2vmin);
      text-transform: lowercase;
    }

    .head-info {
      display: flex;
      flex-flow: row nowrap;
      justify-content: space-between;

      .temperature {
        font-size: calc(50px + 2vmin);
        font-weight: 400;
        letter-spacing: -2px;
      }

      .realfeel {
        font-size: calc(6px + 2vmin);
        font-weight: 400;

        span {
          font-weight: 600;
        }
      }

      .description {
        margin: 2.5vh 0;
        font-size: calc(15px + 2vmin);
      }

      .weather-icon {
        align-self: center;
        width: 145px;
        height: 87px;

        img {
          width: 100%;
          height: auto;
        }
      }
      
    }

    .extra-info {
      font-size: calc(8px + 2vmin);
      line-height: 1.4;
    }
  }// .second section
}